import React from "react";
import Typography from "@material-ui/core/Typography";
import mockList from "../mock.json";
import MatchItem from "../Components/MatchItem.js";
import InfoLine from "../Components/InfoLine.js";

const MatchList = props => {
  const matchList = JSON.parse(localStorage.getItem("matchList")) || {};
  const keys = Object.keys(matchList);
  return (
    <div>
      <Typography>Sua lista de Matches</Typography>
      {keys.map(id => (
        <MatchItem image={matchList[id].image}>
          <InfoLine icon="assignment_ind" text={matchList[id].name} />
          <InfoLine icon="chat" text={matchList[id].whatsapp} />
        </MatchItem>
      ))}
    </div>
  );
};
export default MatchList;
