import React, { useState } from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import axios from "axios";
import { setUser } from "../Service/Firestore";
import { requestPermission } from "../Service/PushNotifications";
import "./Profile.css";

const Profile = props => {
  const defaultImage = "https://avatars2.githubusercontent.com/u/54108471?v=4";
  const StorageData = JSON.parse(localStorage.getItem("userData")) || {};
  const [image, setImage] = useState(StorageData.image || defaultImage);

  const [gitHubUser, setGitHubUser] = useState(StorageData.gitHubUser || "");
  const handleGitHubUserChange = event => {
    setGitHubUser(event.target.value);
  };

  const [name, setName] = useState(StorageData.name || "");
  const handleNameChange = event => {
    setName(event.target.value);
  };

  const [whatsapp, setWhatsapp] = useState(StorageData.whatsapp || "");
  const handleWhatsappChange = event => {
    setWhatsapp(event.target.value);
  };

  const [bio, setBio] = useState(StorageData.bio || "");
  const handleBioChange = event => {
    setBio(event.target.value);
  };

  const getGitHub = () => {
    axios
      .get("https://api.github.com/users/" + gitHubUser)
      .then(response => {
        console.log("github response", response.data);
        const { data } = response;
        const { name, bio, avatar_url, followers, public_repos } = data;
        name && setName(name);
        bio && setBio(bio);
        avatar_url && setImage(avatar_url);
        console.log("Este usuario tem ", followers, " seguidores");
        console.log(
          "Este usuario tem ",
          public_repos,
          " repositórios publicos"
        );
      })
      .catch(error => {
        console.log(error);
      });
    console.log("Cliquei no github");
  };

  const saveChanges = () => {
    const userData = { gitHubUser, name, whatsapp, bio, image };
    localStorage.setItem("userData", JSON.stringify(userData));
    setUser(userData);
    requestPermission();
    console.log("Cliquei em salvar");
  };

  return (
    <div className="Profile-container">
      <Typography>Seu Perfil</Typography>
      <div className="Profile-info">
        <div className="Profile-picture-wrapper">
          <img
            src={image}
            alt="Foto de perfil"
            style={{ height: 140, width: 140 }}
          />
        </div>
        <form className="Profile-flex-full" noValidate autoComplete="off">
          <div className="Profile-form-wrapper">
            <TextField
              className="Profile-flex-full"
              id="github-username"
              value={gitHubUser}
              label="Usuário do GitHub"
              onChange={handleGitHubUserChange}
            />
            <TextField
              className="Profile-flex-full"
              id="nome"
              value={name}
              label="Seu Nome"
              onChange={handleNameChange}
            />
            <TextField
              className="Profile-flex-full"
              id="whatsapp"
              value={whatsapp}
              label="Seu Whatsapp"
              onChange={handleWhatsappChange}
            />
          </div>
        </form>
      </div>
      <div className="Profile-info">
        <TextField
          multiline
          rowsMax="4"
          className="Profile-flex-full"
          id="bio"
          value={bio}
          label="Sua bio"
          onChange={handleBioChange}
        />
      </div>
      <div
        style={{
          display: "flex",
          flex: 1,
          alignItems: "flex-end",
          justifyContent: "space-around"
        }}
      >
        <Button onClick={getGitHub}>Buscar GitHub</Button>
        <Button onClick={saveChanges}>Salvar</Button>
      </div>
    </div>
  );
};
export default Profile;
